import Google from 'ol/source/Google.js';
import Layer from 'ol/layer/WebGLTile.js';
import Map from 'ol/Map.js';
import View from 'ol/View.js';
import { Control, defaults as defaultControls } from 'ol/control.js'; import { fromLonLat } from 'ol/proj';

const lat = 14.566526616029208; // Example longitude
const lon = 121.01504209325368; // Example latitude

// Convert the longitude and latitude to Web Mercator coordinates
const centerCoordinates = fromLonLat([lon, lat]);

function showMap(key) {
  const source = new Google({
    key,
    scale: 'scaleFactor2x',
    highDpi: true,
  });

  source.on('change', () => {
    if (source.getState() === 'error') {
      alert(source.getError());
    }
  });

  // code to add logo
  class GoogleLogoControl extends Control {
    constructor() {
      const element = document.createElement('img');
      element.style.pointerEvents = 'none';
      element.style.position = 'absolute';
      element.style.bottom = '5px';
      element.style.left = '5px';
      element.src =
        'https://developers.google.com/static/maps/documentation/images/google_on_white.png';
      super({
        element: element,
      });
    }
  }

  const map = new Map({
    layers: [new Layer({ source })],
    controls: defaultControls().extend([new GoogleLogoControl()]),
    target: 'map',
    view: new View({
      center: centerCoordinates,
      zoom: 20,
    }),
  });
}

showMap("AIzaSyBjQu-Q3qNLAtrktpgHcmtrH4WLLS7gEo8");

